.bangers {
  font-family: 'Bangers', cursive;
}

.comicNeue {
  font-family: 'Comic Neue', cursive;
}

.germaniaOne {
  font-family: 'Germania One', cursive;
}

.spartan {
  font-family: 'League Spartan', sans-serif;
}

.montserrat {
